function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}

//export default function Hook() {
export default class News extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keyword: "",
      newsTypeList: [{id: 0}],
      currentTabIndex: 0,
      currentPage: 0,
      news: [],
      showMore: false,
      resultText: "",
      value: 0,
    };
  }

  handleChange = (event, newValue) => {
    this.setState(
      {
        currentTabIndex: newValue,
      },
      () => {
        this.handleSearch(this.state.keyword);
      }
    );
  };

  componentDidMount() {
    this.getData();
  }

  renderRedirect = (id) => {
    this.props.history.push(
      `/newsdetails/${id}/${this.props.match.params.token}`
    );
  };

  render() {
    return (
      <div style={styles.wrapperDiv}>
        <SearchBar
          keyword={this.state.keyword}
          onChangeText={this.handleChangeText}
          onSearch={this.handleSearch}
          placeholder="หัวข้อ หรือ เนื้อหาข่าว"
        />

        {this.state.newsTypeList.length > 0 && (
          <div className="space-bottom">
            <AppBar
              position="static"
              color="default"
              style={{
                backgroundColor: "#000",
                color: "#fff",
                clear: "both",
              }}
            >
              <Tabs
                value={this.state.currentTabIndex}
                onChange={this.handleChange}
                indicatorColor="primary"
                textColor="primary"
                variant="scrollable"
                scrollButtons="auto"
                aria-label="scrollable auto tabs example"
              >
                {this.state.newsTypeList.map((item) => {
                  return (
                    <Tab
                      key={item.id}
                      style={{ color: "#efefef" }}
                      label={`${item.name}`}
                      {...a11yProps(this.state.currentTabIndex)}
                    />
                  );
                })}
              </Tabs>
            </AppBar>
          </div>
        )}

        {this.state.news.length === 0 && (
          <h3
            style={{
              marginTop: 8,
              color: "#262626",
              fontWeight: "bold",
              textAlign: "center",
              fontSize: 14,
            }}
          >
            {this.state.resultText}
          </h3>
        )}

        {this.state.news.map((item) => (
          <div
            key={item.id}
            style={{
              marginBottom: 8,
              backgroundColor: "#ffffff",
              borderRadius: 2,
            }}
            onClick={() => {
              if (!item.isRead) {
                RequestUtil.fetchJSON(appConfig.api.news.updateNewsUserAccess, {
                  id: item.id,
                }).then((response) => {});
              }
              if (item.url) {
                window.open(item.url, "_blank");
              } else {
                this.renderRedirect(item.id);
              }
            }}
          >
            {(item.image || !item.isRead) && (
              <div
                style={{
                  width: "100%",
                  height: "auto",
                }}
              >
                {item.image && <img src={item.image} alt="Pic" width="100%" />}
                {!item.isRead && (
                  <div
                    style={{
                      height: 32,
                      backgroundColor: "#eb1c24",
                      opacity: 0.7,
                      bottom: 0,
                      width: "100%",
                      textAlign: "center",
                    }}
                  >
                    <label
                      style={{
                        color: "#ffffff",
                        fontWeight: "bold",
                        fontSize: 14,
                      }}
                    >
                      NEW
                    </label>
                  </div>
                )}
              </div>
            )}
            <div
              style={{
                paddingLeft: 16,
                paddingTop: 8,
                paddingBottom: 8,
              }}
            >
              <label
                style={{
                  fontSize: 18,
                  fontWeight: "bold",
                }}
              >
                {item.title}
              </label>
            </div>
            <div
              style={{
                paddingLeft: 16,
                paddingBottom: 8,
              }}
            >
              <label
                style={{
                  fontSize: 12,
                  color: "#eb1c24",
                }}
              >
                {`Date: ${DateUtil.getFormattedDate(item.date)}`}
              </label>
              <label
                style={{
                  fontSize: 12,
                }}
              >
                {` | By: ${item.postedBy}`}
              </label>
            </div>
          </div>
        ))}

        {this.state.showMore && (
          <button
            style={{
              width: "100%",
              paddingVertical: 8,
              alignItems: "center",
              marginBottom: 8,
              backgroundColor: "#eb1c24",
              borderColor: "#70130a",
              borderWidth: 1,
            }}
            onClick={() => {
              this.handleSearch(this.state.keyword, true);
            }}
          >
            <label
              style={{
                paddingLeft: 2,
                paddingBottom: 4,
                fontWeight: "bold",
                fontSize: 14,
                color: "#fff",
              }}
            >
              แสดงข่าวเพิ่ม
            </label>
          </button>
        )}
      </div>
    );
  }

  handleChangeText = (keyword) => {
    console.log(keyword);
    this.setState({
      keyword,
    });
  };

  handleSearch = (keyword, fetchNextPage = false) => {
    this.setState({
      resultText: "",
    });

    RequestUtil.fetchJSON(
      appConfig.api.news.search,
      this.props.match.params.token,
      {
        q: keyword,
        d: this.state.newsTypeList[this.state.currentTabIndex].id,
        p: fetchNextPage ? this.state.currentPage + 1 : 0,
      }
    )
      .then((response) => {
        console.log("news==>", response);
        if (response.data) {
          if (fetchNextPage) {
            this.setState({
              news: this.state.news.concat(response.data),
              currentPage:
                response.data.length > 0
                  ? this.state.currentPage + 1
                  : this.state.currentPage,
              showMore: response.data.length >= 10,
            });
          } else {
            this.setState(
              {
                news: response.data,
                currentPage: 0,
                showMore: response.data.length >= 10,
                resultText: response.data.length > 0 ? "" : "ไม่พบข้อมูล",
              }
              //() => this.scrollViewRef.scrollTo({ x: 0, y: 0, animated: false })
            );
          }
        } else {
          console.log("error");
        }
      })
      .catch((error) => {
        this.setState({
          news: [],
          currentPage: 0,
          showMore: false,
          resultText: "เกิดข้อผิดพลาด กรุณาลองอีกครั้ง",
        });
      });
  };

  getData() {
    RequestUtil.fetchJSON(
      appConfig.api.news.getDepartments,
      this.props.match.params.token,
      {}
    )
      .then((response) => {
        if (response.data) {
          this.setState(
            {
              newsTypeList: response.data,
            },
            () => {
              this.handleSearch(this.state.keyword);
            }
          );
        } else {
          console.log("error!!!");
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
}

var styles = {
  wrapperDiv: {
    backgroundColor: "#dfdfdf",
  },
};
